/* --- Container holding iNaturalist + FlagCounter --- */
.inat-flag-container {
  display: flex;
  flex-direction: row;       /* side by side */
  align-items: flex-start;   /* align tops */
  gap: 24px;                 /* space between flag counter and iNat */
  margin-bottom: 20px;
}

/* Mobile: stack them vertically */
@media (max-width: 768px) {
  .inat-flag-container {
    flex-direction: column;
    align-items: flex-start;
    margin: 16px 0;
  }
}

/* --- iNaturalist widget placement --- */
.inat-widget,
.inat-widget a,
.inat-label,
.inat-value,
.inat-user {
  font-family: inherit !important;
}

/* Hide the iNaturalist header/logo block */
.inat-widget-header {
  display: none !important;
}

/* Remove borders/boxes the script adds */
.inat-widget table {
  border-collapse: collapse !important;
  width: 100%;
}
.inat-widget tr,
.inat-widget td {
  border: none !important;
  background: transparent !important;
  box-shadow: none !important;
}

/* Image niceties */
.inat-observation-image img,
.inat-user-image img {
  max-width: 48px !important;
  border-radius: 6px;
  vertical-align: middle;
}

/* Spacing between rows */
.inat-widget td {
  vertical-align: top;
  padding: 6px 0 !important;
}

/* --- FlagCounter styling --- */
.flagcounter {
  flex: 0 0 auto;        /* donâ€™t let it stretch or shrink */
  max-width: 200px;      /* keeps flag counter narrow */
}

.flagcounter img {
  max-width: 100%;
  height: auto;
  border: none;
}
